.container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
  border: 1px solid red;
}

.card {
  border: 1px solid green;
  height: 200px;
  width: 20%;
  padding: 10px;
  box-sizing: border-box;

}

.item {
  background-color: yellow;
  border: 1px solid grey;
  align-items: center;
  height: 100%;
  width: 100%;
  justify-content: center;
  font-size: 30px;
  display: flex;

  

}

@media screen and (max-width: 1024px) {
  .container {
    width: 33%;
  }
}

@media screen and (max-width: 768px) {
  .container {
    width: 50%;
  }
}